.tagBtn {
  background-color: var(--dark-white);
  border-radius: 1rem;
  color: var(--blue);

  text-align: center;

  font-weight: 600;
  font-size: 1.3rem;
  line-height: 1.9rem;

  padding: 0.5rem 1.6rem;
  cursor: pointer;
}

.tagBtn:hover {
  background-color: hsl(230, 100%, 90%);
}
